.ui-accordion {
  border-radius: 0px;
  border: 0px;
}

.ui-accordion .ui-accordion-header {
  padding: .5em .5em .5em .7em;
  text-decoration: none;
  font-size: 14px;
  line-height: 20px;
  border-radius: 0px;
  padding-left: 2.2em;
  margin-bottom: 0rem;
  background: transparent;
  border: none;
  color: color("azul-oscuro-brc");
  font-weight: 600;
  border-bottom: 1px solid color("grey-light");
}

.ui-accordion .ui-accordion-content {
  padding: 0.7em 0.2em;
  border-top: 0;
  border-radius: 0px;
  border: none;
  padding-left: 2.2em;
}

.ui-accordion .ui-accordion-content .view {
  font-size: 13px;
}

.ui-accordion .ui-accordion-content .user-info {
  padding: 12px 6px 12px 8px;
  border-bottom: 1px dotted #e2e2e2;
}
